import { mapState, mapMutations, mapActions } from 'vuex'
import OrderList from '../../../components/orderListTemplate'
import LoadingMore from '../../../components/loadingMoreTemplate'

export default {
  data () {
    return {
      page: 1,
      status: false,
      currentIndex: 1
    }
  },
  onLoad (options) {
    this.page = 1
    this.status = false
    this.currentIndex = 1
    this.getOrderListFn(true)
    if (options.status) this.status = true
  },
  // 下拉刷新
  onPullDownRefresh () {
    this.page = 1
    this.getOrderListFn(true)
  },
  // 上拉加载
  onReachBottom () {
    this.page += 1
    this.getOrderListFn(false)
  },
  computed: {
    ...mapState([
      'userInfo',
      'orderList'
    ])
  },
  methods: {
    ...mapMutations({
      setOrderList: 'SET_ORDER_LIST'
    }),
    ...mapActions([
      'getOrderList'
    ]),
    // 选择类型
    onSelectItemFn (index) {
      this.setOrderList([])
      this.page = 1
      this.currentIndex = index
      this.getOrderListFn(true)
    },
    // 获取订单列表
    getOrderListFn (isLoading) {
      const data = {
        that: this,
        page: this.page,
        type: this.currentIndex
      }
      if (isLoading) {
        this.setOrderList([])
        uni.showLoading({ title: 'Loading' })
      }
      uni.showNavigationBarLoading()
      this.getOrderList(data).then(() => {
        if (isLoading) uni.hideLoading()
        uni.stopPullDownRefresh()
        uni.hideNavigationBarLoading()
      })
    }
  },
  onUnload () {
    this.setOrderList([])
    if (!this.status) uni.switchTab({ url: '/pages/me/index' })
  },
  components: {
    OrderList,
    LoadingMore
  }
}
